import { injectGlobal } from 'styled-components';

import theme from './theme';

injectGlobal`
  html, body {
    width: 100%;
    height: 100%;
  }

  body {
    font-family: ${theme.font};
    color: ${theme.colors.text};
  }

  #___gatsby {
    height: 100%;
  }
`;



