import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { Provider } from 'react-redux';
// import webfontloader from 'webfontloader';

import Footer from '../containers/Footer';
import Box from '../components/Box';
import ThemeProvider from '../components/ThemeProvider';
import createStore from '../state/createStore';

// if (typeof window !== 'undefined') {
//   webfontloader.load({
//     google: {
//       families: ['Dosis:200,400,700', 'Montserrat']
//     },
//   });
// }

const Layout = ({ children, data }) => (
  <Provider store={createStore()}>
    <Box height="100%">
      <Helmet>
        <title>{data.site.siteMetadata.title}</title>
        <meta name="description" content="我們是一家資訊設計公司，讓你的資訊透過設計，打動人心。" />
        <meta name="google-site-verification" content="XipMl6VnRHFU_QK8_7cDItQ1xv-DrI17edh4SX2j00o" />
        <link rel="apple-touch-icon" sizes="180x180" href="/apple-touch-icon.png" />
        <link rel="icon" type="image/png" sizes="32x32" href="/favicon-32x32.png" />
        <link rel="icon" type="image/png" sizes="16x16" href="/favicon-16x16.png" />
        <link rel="manifest" href="/site.webmanifest" />
        <link rel="mask-icon" color="#5bbad" href="/safari-pinned-tab.svg" />
        <meta name="msapplication-TileColor" content="#da532c" />
        <meta name="theme-color" content="#ffffff" />
        <meta property="og:url" content="https://relab.cc" />
        <meta property="og:title" content="Re-lab" />
        <meta property="og:description" content="我們是一家資訊設計公司，讓你的資訊透過設計，打動人心。" />
        <meta property="og:image" content="https://relab.cc/fb.png" />
        <link href="https://fonts.googleapis.com/css?family=Dosis:200,400,700|Montserrat" rel="stylesheet"></link>
      </Helmet>
      <ThemeProvider>
        {children()}
        <Footer />
      </ThemeProvider>
    </Box>
  </Provider>
);

Layout.propTypes = {
  children: PropTypes.func,
  data: PropTypes.object,
};

export default Layout;

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`;



