import React from 'react';

import FaFacebookF from 'react-icons/lib/fa/facebook';
import FaBehance from 'react-icons/lib/fa/behance';
import FaVimeoV from 'react-icons/lib/fa/vimeo';

import withResponsive from '../../hoc/withResponsive';

import Container from '../../components/Container';
import Box from '../../components/Box';
import Circle from '../../components/Circle';
import Text from '../../components/Text';
import Flex from '../../components/Flex';
import Link from '../../components/Link';
import BackgroundImage from '../../components/BackgroundImage';

import relabyo from './relabyo.svg';
import footerbubble from './footer-bubble.svg';

const linkIcons = [
  {
    Icon: FaFacebookF,
    href: 'https://www.facebook.com/ReLAB.cc/',
  },
  {
    Icon: FaVimeoV,
    href: 'https://vimeo.com/relab',
  },
  {
    Icon: FaBehance,
    href: 'https://www.behance.net/Re-LAB',
  }
];

const Bubble = () => <BackgroundImage w="100%" src={footerbubble} ratio={200 / 430} />;

const Footer = ({ browser }) => (
  <Box bg="blue" px="1em">
    <Container py="3em">
      {!browser.greaterThan.xs && (
        <Box px="2em" mb="2em">
          <Bubble />
        </Box>
      )}
      <Flex justify="center">
        <Box>
          <Box w="40%" ml="-0.5em">
            <Link to="/">
              <BackgroundImage src={relabyo} ratio={42 / 130} />
            </Link>
          </Box>
          <Box color="white">
            <Box pb="1em" pt="2em">
              <Link
                color="white"
                fontWeight="normal"
                hoverColor="white"
                href="mailto:hi@relab.cc"
              >
                hi@relab.cc
              </Link >
            </Box>
            <Text>106台北市敦化南路一段160巷58號3F</Text>
            <Text>3F., No.58, Ln. 160,Sec. 1, Dunhua S. Rd., Da'an Dist. Taipei City 106, Taiwan.</Text>
            <Flex pt="1.5em">
              {linkIcons.map(({ Icon, href }, index) => (
                <Box mr="1em" key={index}>
                  <Link href={href} target="_blank">
                    <Circle f="1.25em" w="2.5em" border="1px solid" borderColor="white" color="white">
                      <Icon />
                    </Circle>
                  </Link>
                </Box>
              ))}
            </Flex>
            <Text pt="0.5em">Copyright © 2018. All rights reserved.</Text>
          </Box>
        </Box>
        {browser.greaterThan.xs && (
          <Box w="30%" ml="4em" my="auto">
            <Bubble />
          </Box>
        )}
      </Flex>
    </Container>
  </Box>
);

export default withResponsive(Footer);




